<style lang="less" scoped>
.nav-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #fff;
  width: 100%;
  position: sticky;
  top: -3px;
  z-index: 3;
  & > div {
    width: 50%;
    text-align: center;
    height: 90px;
    line-height: 90px;
    font-size: 35px;
    cursor: pointer;
  }
  & > div.nav-wrapper {
    position: relative;
    &::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 50%;
      width: 100px;
      height: 4px;
      background-color: #000;
      transform: translateX(-50%);
    }
  }
}
.fixed-nav {
  position: fixed !important;
  top: 0;
  z-index: 5;
  width: 750px !important;
}
</style>
<template>
    <div v-header-fixed class="nav-container">
        <div :class="{ 'nav-wrapper': index === 0 && HOME_CATEGORY_STATUS === 'j5m484vz' ? true : false }" v-for="(navItem, index) in HOME_CATEGORY_LIST" v-if="index === 0" :key="index" @click="modifiedCategory(navItem)">{{navItem.name}}</div>
				<div :class="{ 'nav-wrapper': index === 1 && HOME_CATEGORY_STATUS === 'j5m484vz' ? false : true }" v-for="(navItem, index) in HOME_CATEGORY_LIST" v-if="index === 1" :key="index" @click="modifiedCategory(navItem)">{{navItem.name}}</div>
    </div>
</template>
<script>
import { createNamespacedHelpers } from "vuex";
const { mapMutations, mapActions, mapState } = createNamespacedHelpers(
  "homeModule"
);
export default {
  data() {
    return {
      headerNavStatus: true
    };
  },
  methods: {
    ...mapActions(["GET_HOME_CATEGORY"]),
    ...mapMutations(["HOME_HEADER_NAV", "CLEAN_HOME_DATA"]),
    modifiedCategory(item) {
      let routeData = this.$route.query.category;
      if (item.categoryId === routeData) return;

      this.HOME_HEADER_NAV(item.categoryId);
      let homeData = this.$store.state.homeModule.HOME_DATA_OBJECT;
      if (this.$route.query.type) {
        this.$router.push(
          `?&category=${item.categoryId}&type=${this.$route.query.type}`
        );
      } else {
        this.$router.push(`?&category=${item.categoryId}&type=SXK`);
      }
      this.CLEAN_HOME_DATA();
    }
  },
  computed: {
    ...mapState(["HOME_CATEGORY_LIST"]),
    HOME_CATEGORY_STATUS() {
      return this.$store.state.homeModule["HOME_HEADER_NAV"] || "j5m484vz";
    }
  },
  created() {
    if (this.HOME_CATEGORY_LIST.length === 0) {
      this.GET_HOME_CATEGORY();
    }
    let category = this.$route.query.category;
    if (category && category.includes("j5m484vz")) {
      this.headerNavStatus = false;
      this.HOME_HEADER_NAV(category);
    } else {
      this.headerNavStatus = true;
      this.HOME_HEADER_NAV(category);
    }
  }
};
</script>




