<style lang="less" scoped>
.course-nav-container{
	width: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: center;
	background-color: #fff;
	& > div{
		width: 50%;
		height: 100px;
		text-align: center;
		& > button{
			cursor: pointer;
			border: none;
			outline: none;
			background-color: transparent;
			width: 250px;
			height: 100px;
			background-repeat: no-repeat;
			background-position: 0 15px;
			background-size: contain;
		}
	}
}
</style>
<template>
    <div class="course-nav-container">
			<div v-for="(navItem, index) in courseNav" :key="index">
				<button @click="modifiedNavStatusT" v-if="index === 0" :style="{backgroundImage: 'url(' + (navStatus ? navItem.activePic : navItem.beforePic) + ')'}"></button>
				<button @click="modifiedNavStatusV" v-if="index === 1" :style="{backgroundImage: 'url(' + (!navStatus ? navItem.activePic : navItem.beforePic) + ')'}"></button>
			</div>
    </div>
</template>
<script>
import { createNamespacedHelpers } from "vuex";
const { mapMutations, mapState } = createNamespacedHelpers(
  "homeModule"
);
export default {
	data () {
		return {
			navStatus: true,
			courseNav: [
				{
					beforePic: require('../assets/img/sx.png'),
					activePic: require('../assets/img/sx_g.png')
				},
				{
					beforePic: require('../assets/img/vip_h.png'),
					activePic: require('../assets/img/vip_g.png')
				}
			]
		}
	},
	methods: {
		...mapMutations([
			'HOME_MIDDLE_NAV',
			'CLEAN_HOME_DATA'
		]),
		modifiedNavStatusT(){
			if(this.$route.fullPath !== `/?category=${this.$route.query.category}&type=SXK`){
				this.CLEAN_HOME_DATA()
			}
			this.navStatus = true
			// 重置课程数据
			
			this.HOME_MIDDLE_NAV('SXK')
			if(this.$route.query.category){
				this.$router.push(`/?category=${this.$route.query.category}&type=SXK`)
			}else{
				this.$router.push(`/?category=j5m484vz&type=VIP`)
			}
			
		},
		modifiedNavStatusV(){
			if(this.$route.fullPath !== `/?category=${this.$route.query.category}&type=VIP`){
				this.CLEAN_HOME_DATA()
			}
			this.navStatus = false
			// 重置课程数据
			this.HOME_MIDDLE_NAV('VIP')
			if(this.$route.query.category){
				this.$router.push(`/?category=${this.$route.query.category}&type=VIP`)
			}else{
				this.$router.push(`/?category=j5m484vz&type=VIP`)
			}
		}
	},
	created () {
		if(this.$route.query.type && this.$route.query.type.includes('VIP')){
			this.navStatus = false
			this.HOME_MIDDLE_NAV('VIP')
		}else{
			this.navStatus = true
			this.HOME_MIDDLE_NAV('SXK')
		}
	}
};
</script>



