<style lang="less" scoped>
.home-header {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: #fff;
  height: 100px;
  & > div {
    &:nth-child(1) {
      width: 60%;
      & > img {
        width: 220px;
        margin-left: 20px;
      }
    }
    &:nth-child(2) {
      width: 20%;
      text-align: center;
      & > img {
        width: 60px;
      }
    }
    &:nth-child(3) {
      width: 20%;
      text-align: center;
      & > img {
        width: 60px;
      }
      & > span {
        color: rgb(63, 177, 64);
        display: inline-block;
        margin-left: -3px;
        font-size: 35px;
        line-height: 100px;
      }
    }
  }
}
</style>
<template>
    <div class="home-header">
        <div><img src="../assets/img/logo.png" alt="logo"></div>
        <div><img src="../assets/img/sousuo.png" alt="搜索" @click="searchCourses"></div>
        <div>
            <img src="../assets/img/message.png" alt="我的消息" @click="enterMyNews" v-if="LOGIN_USER_OBJECT">
            <span v-if="!LOGIN_USER_OBJECT" @click="showLoginModal">登录</span>
        </div>
        <login v-if="!LOGIN_USER_OBJECT && loginModalStatus" @showDialogEvent="showDialogEvent"></login>
    </div>
</template>
<script>
import login from "@/components/login";
import { createNamespacedHelpers } from "vuex";
const { mapMutations, mapActions, mapState } = createNamespacedHelpers(
  "userModule"
);
export default {
  components: {
    login
  },
  computed: {
    ...mapState(["LOGIN_USER_OBJECT"])
  },
  data() {
    return {
      loginModalStatus: false
    };
  },
  methods: {
    showLoginModal() {
      this.loginModalStatus = true;
    },
    showDialogEvent(data) {
      this.loginModalStatus = data;
    },
    searchCourses() {
      this.$router.push({
        name: 'searchWorks'
      });
    },
    enterMyNews(){
      this.$router.push({
        name: 'myNews'
      })
    }
  }
};
</script>



