<style lang="less" scoped>
@import url("../assets/css/studyPage.less");
</style>
<template>
<section class="course_list">
  <div class="course_flex">
    <div class="flex_middle" v-for="(notRecommendItem , index) in notRecommendList" v-if="notRecommendItem.isRecommended === 'N'" :key="index" @click="course_list(notRecommendItem)">
		<div class="course_header_img">
				<img :src="notRecommendItem.coverImage" :alt="notRecommendItem.keywords" :title="notRecommendItem.keywords">
				<div class="flex_middle-img"><img v-if="notRecommendItem.courseType !== 'VIP'" class="live-img" :src="notRecommendItem.liveStatus != 'LIVING' ? pause_img : live_img " :alt="notRecommendItem.liveStatus != 'LIVING' ? '无直播' : '直播'"></div>
		</div>
		<div>
				<div class="title">{{notRecommendItem.title}}</div>
		</div>
		<div class="course_text_detail">
				<div>
					<p>{{notRecommendItem.liveStatus == "LIVING" ? "正在直播" : "暂无直播"}}</p>
					<p class="course_text_detail_pad"><img src="../assets/img/people1.png" width="20px" class="course_text_detail_pad_img">{{notRecommendItem.totalStudent | numFm}}</p>
				</div>
				<div class="coll">
					<img v-if="notRecommendItem.isFollowed == 'N'" :src="collection" @click.stop="favorite(notRecommendItem)">
					<img v-if="notRecommendItem.isFollowed == 'Y'" :src="flowed" @click.stop="favorite(notRecommendItem)">
				</div>
		</div>
  </div>
  <login v-if="!LOGIN_USER_OBJECT && loginModalStatus" v-on:showDialogEvent="showDialogEvent"></login>
  </div>
</section>
</template>
<script>
import login from '@/components/login'
export default {
	components: {
		login
	},
  props: ["notRecommendList"],
  data() {
    return {
      collection: require("../assets/img/je3sg446.png"),
      flowed: require("../assets/img/je3sgvx4.png"),
      live_img: require("../assets/img/151963024645632.gif"),
      pause_img: require("../assets/img/je3shpb8.png"),
      loginModalStatus: false
    };
  },
  computed: {
    LOGIN_USER_OBJECT() {
      return this.$store.state.userModule.LOGIN_USER_OBJECT;
    }
  },
  methods: {
		showDialogEvent(data) {
      this.loginModalStatus = false;
    },
    course_list(info) {
      if (!this.LOGIN_USER_OBJECT && info.courseType !== 'VIP') {
        this.loginModalStatus = true;
        return
      }
      if(info.courseType === 'VOD'){
        // 正在直播并已登录情况下可进入
        if (info.liveStatus == "LIVING") {
          this.$router.push({
            name: "live",
            params: {
              id: info.defaultLiveChannel,
              courseId: info.courseId
            }
          });
        } else {
          this.$Toast({
            message: "暂无直播",
            position: "middle",
            duration: 1000
          });
        }
      }else if(info.courseType === 'VIP'){
        this.$router.push({
          name: 'vipDetail',
          params: {
            id: info.courseId
          }
        })
      }
    },
    addFavoriteCourse(item) {
      this.axios
        .post("/api/favorite", {
          courseId: item.courseId
        })
        .then(data => {
          if (data.data === "添加成功") {
            this.$set(item, "isFollowed", "Y");
            this.$Toast({
              message: data.data,
              position: "middle",
              duration: 1000
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    deleteFavoriteCourse(item) {
      this.axios
        .post("/api/favorite/delete", {
          courseId: item.courseId
        })
        .then(data => {
          if (data.data === "移除成功") {
            this.$set(item, "isFollowed", "N");
            this.$Toast({
              message: data.data,
              position: "middle",
              duration: 1000
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    },
    // 收藏课程或移除收藏课程
    favorite(item) {
      if (!this.LOGIN_USER_OBJECT) {
        this.loginModalStatus = true;
        return;
      }
      if (item.isFollowed == "N") {
        // 收藏课程
        this.addFavoriteCourse(item);
      } else {
        // 移除收藏的课程
        this.deleteFavoriteCourse(item);
      }
		}
  }
};
</script>



