<style lang="less" scoped>
@media screen and (max-width: 1000px){
	.banner-container{
		width: 100%;
		.swiper-container{
			height: 300px;
			& > .swiper-wrapper{
				& > .swiper-slide{
					& > img{
						width: 100%;
						height: 300px;
					}
				}
			}
		}
	}
}
@media screen and(min-width: 1000px){
	.banner-container{
		width: 100%;
		.swiper-container{
			height: 350px;
			& > .swiper-wrapper{
				& > .swiper-slide{
					& > img{
						width: 100%;
						height: 350px;
					}
				}
			}
		}
	}
}
</style>
<template>
    <div class="banner-container">
		<swiper :options="swiperOption" ref="mySwiper">
          <!-- slides -->
          <swiper-slide v-for="item in BANNER_DESIGN_LIST" :key="item.carouselFigureId">
            <img :src="item.carouselFigureSmallImage" @click="select_carousel(item)">
          </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>
<script>
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState } = createNamespacedHelpers("homeModule");
export default {
	data () {
		return {
			swiperOption: {
				autoplay: true,
				slidesPerView: 1,
				pagination: {
					el: '.swiper-pagination',
				}
			},
			picType: 'MOBILEHEADERPIC'
		}
	},
  methods: {
		...mapActions(["BANNER_DESIGN"]),
		select_carousel(item) {
			if (item.carouselFigureLink == '#') {
				return
			}else if(item.carouselFigureLink.indexOf('http') !== -1){
				window.location.href = item.carouselFigureLink
				return
			}
			this.$router.push({
				name: 'bannerComp',
				params: {
					id: item.carouselFigureId
				}
			});
		}
  },
  computed: {
		...mapState([
				'BANNER_DESIGN_LIST',
				'HOME_HEADER_NAV'
			]),
		swiper() {
			return this.$refs.mySwiper.swiper
		}
	},
	watch: {
		HOME_HEADER_NAV(data){
			this.BANNER_DESIGN(
				{
					picType: this.picType,
					categoryId: this.$route.query.category || 'j5m484vz'
				}
			)
		}
	},
  created() {

	//   if(this.BANNER_DESIGN_LIST.length > 0){
		  this.BANNER_DESIGN(
				{
					picType: this.picType,
					categoryId: this.$route.query.category || 'j5m484vz'
				}
			)
	//   }
  }
};
</script>



