<style lang="less" scoped>
@import url('../assets/css/studyPage.less');
</style>
<template>
    <div>
        <div class="live_item" @click="course_list(recommendItem)">
        <div class="content_bottom content_bottom_one">
            <div class="middel_img_header">
            <div class="middel_img_header_text">
                <div class="live-img-parent"><img class="live-img" :src="recommendItem.liveStatus != 'LIVING' ? pause_img : live_img " :alt="recommendItem.liveStatus != 'LIVING' ? '无直播' : '直播'"></div>
            </div>
            <div class="middel_img_header_right_img">
                <img :src="recommendItem.recommendImage" alt="课程">
            </div>
            </div>
            <div class="middle_img_footer">
            <div class="middle_detail">
                <div>
                <span> <span>{{recommendItem.title}}</span> </span>
                <span>{{recommendItem.totalStudent | numFm}}人在看</span>
                </div>
            </div>
            <div class="middle_attention">
                <img v-if="recommendItem.isFollowed == 'N'" :src="collection" @click.stop="favorite(recommendItem)">
                <img v-if="recommendItem.isFollowed == 'Y'" :src="flowed" @click.stop="favorite(recommendItem)">
            </div>
            </div>
        </div>
        </div>
        <login v-if="!LOGIN_USER_OBJECT && loginModalStatus" @showDialogEvent="showDialogEvent"></login>
    </div>
</template>
<script>
import login from '@/components/login'
export default {
    components: {
        login
    },
    data () {
        return {
            loginModalStatus: false,
            pause_img: require('../assets/img/je3shpb8.png'),
            live_img: require('../assets/img/151963024645632.gif'),
            collection: require('../assets/img/je3sg446.png'),
            flowed: require("../assets/img/je3sgvx4.png"),
        }
    },
    props: [
        'recommendItem'
    ],
    computed: {
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }  
    },
    methods: {
        showDialogEvent(data) {
            this.loginModalStatus = data
        },
        course_list(info) {
            if (!this.LOGIN_USER_OBJECT) {
                this.loginModalStatus = true
                return;
            }
            // 正在直播并已登录情况下可进入
            if (info.liveStatus == 'LIVING') {
            this.$router.push({
                name: 'live',
                params: {
                    id: info.defaultLiveChannel,
                    courseId: info.courseId,
                }
            });
            } else {
                this.$Toast({
                    message: '暂无直播',
                    position: 'middle',
                    duration: 1000
                })
            }
        },
        addFavoriteCourse(item) {
            this.axios.post('/api/favorite', {
            courseId: item.courseId
            }).then(data => {
            if (data.data === '添加成功') {
                this.$set(item, 'isFollowed', 'Y')
                this.$Toast({
                message: data.data,
                position: 'middle',
                duration: 1000
                })
            }
            }).catch(err => {
            console.log(err)
            });
        },
        deleteFavoriteCourse(item) {
            this.axios.post('/api/favorite/delete', {
            courseId: item.courseId
            }).then(data => {
            if (data.data === '移除成功') {
                this.$set(item, 'isFollowed', 'N')
                this.$Toast({
                message: data.data,
                position: 'middle',
                duration: 1000
                })
            }
            }).catch(err => {
            console.log(err)
            });
        },
        // 收藏课程或移除收藏课程
        favorite(item) {
            if (!this.LOGIN_USER_OBJECT) {
            this.loginModalStatus = true
            return;
            }
            if (item.isFollowed == 'N') {
            // 收藏课程
            this.addFavoriteCourse(item)
            } else {
            // 移除收藏的课程
            this.deleteFavoriteCourse(item)
            }
        }
    }
}
</script>



