<style lang="less" scoped>
@import url("../assets/css/studyPage.less");
.course-container{
    padding-bottom: 120px;
}
</style>
<style>
body{
    background-color: #fff;
}
</style>

<template>
    <div class="course-container" >
        <recommend-course v-if="recommendItem.isRecommended === 'Y' " v-for="(recommendItem, index) in HOME_DATA_OBJECT.HOME_DATA_LIST" :key="index" :recommendItem="recommendItem"></recommend-course>
        <not-recommend-course :notRecommendList="HOME_DATA_OBJECT.HOME_DATA_LIST"></not-recommend-course>
    </div>
</template>
<script>
import { createNamespacedHelpers } from "vuex";
const { mapActions, mapState } = createNamespacedHelpers(
  "homeModule"
);
import RecommendCourse from '@/components/recommendCourse'
import NotRecommendCourse from '@/components/notRecommendCourse'
export default {
    data () {
        return {
            loadMoreStatus: true
        }
    },
    components: {
        RecommendCourse,
        NotRecommendCourse
    },
    computed: {
      ...mapState([
        'HOME_DATA_OBJECT',
        'HOME_HEADER_NAV',
        'HOME_MIDDLE_NAV'
      ]),
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      }
    },
    watch: {
        '$route'(data){
            // 重置是否可分页状态
            this.loadMoreStatus = true
            this.$store.state.homeModule.HOME_DATA_OBJECT.HOME_DATA_LIST = []
            this.$Indicator.open({
                text: '加载中...',
                spinnerType: 'fading-circle'
            })
            this.HOME_DATA_OBJECT_LIST({
                page: 1,
                pagesize: 10,
                courseTypeId: this.HOME_HEADER_NAV || 'j5m484vz',
                type:  this.$route.query.type || 'SXK'
            }).then(data => {
                this.$Indicator.close()
            })
        },
        LOGIN_USER_OBJECT(){
            // 重置是否可分页状态
            this.loadMoreStatus = true
            let params = {
                page: 1,
                pagesize: 10,
                courseTypeId: this.$route.query.category || 'j5m484vz',
                type: this.$route.query.type || 'SXK'
            }
            this.HOME_DATA_OBJECT_LIST( params )
        }
    },
    methods: {
        ...mapActions([
            'HOME_DATA_OBJECT_LIST'
        ]),
        loadMore(){
            if(this.loadMoreStatus){
                this.loadMoreStatus = false

                this.loading()

                this.HOME_DATA_OBJECT_LIST({
                    page: ++this.$store.state.homeModule.HOME_DATA_OBJECT.HOME_DATA_PAGE,
                    pagesize: 10,
                    courseTypeId: this.HOME_HEADER_NAV || 'j5m484vz',
                    type: this.$route.query.type || 'SXK'
                }).then(res => {
                    this.hideLoading()
                    if(!Array.isArray(res.data.result) || res.data.result.length === 0){
                        this.$Toast({
                            message: '没有更多课程了',
                            position: 'middle',
                            duration: 1000
                        })
                        this.loadMoreStatus = false
                    }else{
                        this.loadMoreStatus = true
                    }
                    this.$Indicator.close()
                }).catch(err => {
                    console.log(err)
                })
            }
        }
    },
    created () {

        // if(this.$store.state.homeModule.HOME_DATA_OBJECT.HOME_DATA_LIST.length > 0) return

        this.loading()

        this.$store.state.homeModule.HOME_DATA_OBJECT.HOME_DATA_LIST = []
        this.$store.state.homeModule.HOME_DATA_OBJECT.HOME_DATA_PAGE = 1

        this.HOME_DATA_OBJECT_LIST({
            page: 1,
            pagesize: 10,
            courseTypeId: this.$route.query.category || 'j5m484vz',
            type: this.$route.query.type || 'SXK'
        }).then(() => {
            this.hideLoading()
        })
    },
    destroyed () {
      window.onscroll = () => {
          return
      }  
    },
    mounted () {
        window.onscroll = () => {
            if(this.scrollEvent()){
                this.loadMore()
            }
        }
    }
}
</script>



